<template>
    <div class="container">
        <table>
            <tr>
                <th>아이디</th>
                <td>{{id}}</td>
            </tr>
            <tr>
                <th>비밀번호</th>
                <td><input type="text" v-model="pw" /></td>
            </tr>
            <tr>
                <th>제목</th>
                <td><input type="text" v-model="title" /></td>
            </tr>
            <tr>
                <th>내용</th>
                <td><input type="text" v-model="content" /></td>
            </tr>
        </table>
        <div class="clearAllContainer">
            <span class="clearAllBtn" @click="addqna">작성하기</span>&nbsp;&nbsp;
            <span class="clearAllBtn" @click="back">돌아가기</span>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            id: "",
            pw: "",
            title: "",
            content: "",
        };
    },
     created() {
        this.id = this.$store.state.id;
    },
    methods: {
		addqna(){
			if(this.content != ""){
                //action call
                this.$store.dispatch("ADDQNA", {
                    id: this.id,
                    pw: this.pw,
                    title: this.title,
                    content: this.content,
                });//insert
                this.pw = "";
                this.title = "";
                this.content = "";
                this.$router.push("/");
            }else{
                alert("추가할 내용을 입력해 주세요.")
            }
	    },
        back(){
            this.$router.push("/");
        }
    },
};
</script>

